<template>
	<view class="list">
		<view class="wrap">
			<view class="item u-border-bottom u-padding-bottom-24 u-padding-top-24 " @click="onClick(item.id)" v-for="(item,index) in list"
			 :key="index">
				<view class="u-flex" v-if="randomIndex == index" style="flex-direction: column;">
					<u-image width="100%" height="318rpx" border-radius="6rpx" :src="item.pic+'?imageMogr2/crop/216x148'"></u-image>
					<view class="info u-flex-1 u-m-t-24" style="width: 100%;">
						<view class="tit u-line-2">{{item.title}}</view>
						<view class="time u-m-t-12">
							<u-tag v-if="item.btnName" :text="item.btnName" type="primary" size="mini" style="margin-right: 12rpx;" /> 阅读量·{{item.readNum|million}}
							转发量·{{item.shareNum|million}}</view>
					</view>
				</view>
				<view class="u-flex" v-else>
					<view class="info u-flex-1 u-m-r-12">
						<view class="tit u-line-2">{{item.title}}</view>
						<view class="time u-m-t-12">
							<u-tag v-if="item.btnName" :text="item.btnName" type="primary" size="mini" style="margin-right: 12rpx;" /> 阅读量·{{item.readNum|million}}
							转发量·{{item.shareNum|million}}</view>
					</view>
					<u-image width="202rpx" height="146rpx" border-radius="6rpx" :src="item.pic+'?imageMogr2/crop/216x148'"></u-image>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		props: {
			item: {
				type: Object
			},
			showName: {
				type: [String, Number],
				default: 0
			},
			list: {
				type: [Array, String],
				default: []
			},
			randomIndex:{
				type:Number,
				default:1
			}
		},
		filters: {
			million(value) {
				if (!value) {
					value = 0
				}
				let num;
				if (value > 9999) {
					num = (Math.floor(value / 1000) / 10) + 'W'
				} else if (value < 9999) {
					num = value
				}
				return num
			}
		},
		methods: {
			onClick(id) {
				this.$emit('click', id)
			}
		},
		onShow() {
			console.log('切换了')
		},
	}
</script>

<style lang="scss">
	.list {
		background-color: #fff;
		padding: 0 24rpx;
		border-radius: 12rpx;
	}

	.tit {
		line-height: 44rpx;
		font-size: 28rpx;
	}

	.time {
		color: #999;
		font-size: 24rpx;

		text {
			margin-right: 8rpx;
			padding: 0 8rpx;
			border-radius: 4rpx;
			display: inline-block;
			line-height: 34rpx;
		}
	}

	.item {
		// display: flex;
		// align-items: center;
		// justify-content: space-between;
		// font-size: 0;

		&:last-child::after {
			display: none;
		}
		&>view{
			height: 100%;
			width: 100%;
		}
		.info {
			// flex: 1;
			// display: flex;
			// flex-direction: column;
			// justify-content: space-between;
			// height: 146rpx;
			// margin-right: 24rpx;
		}

		image {
			width: 204rpx;
			height: 146rpx;
			border-radius: 6rpx;
			flex-shrink: 0;
			margin-left: 24rpx;
		}
	}
</style>
